import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.css']
})
export class SidebarComponent implements OnInit {

  private opened = false;
  private csusUrl = 'http://www.csus.edu';
  private ecsUrl = 'http://www.ecs.csus.edu';
  private gitHubUrl = 'https://www.github.com/rbwrightjr91';
  private npmjsUrl = 'https://www.npmjs.com/~rbwrightjr91';
  private linkedInUrl = 'https://www.linkedin.com/in/rbwrightjr91';

  constructor() { }

  ngOnInit() { }

  private toggleSidebar() {
    this.opened = !this.opened;
  }

}



